import JsonP from 'jsonp';
import axios from 'axios';
import { message } from 'antd';
import { Config } from '../config/public';
import { common } from '../common/public';

interface options {
	url: string;
	method?: any;
	data?: any; //post参数
	params?: any; //get参数
	loading?: boolean;
}
/**
 * axios封装
 * @export
 * @class Axios
 */
export default class requestAjax {
	/**
	 * jsonP方法
	 * @static
	 * @param {*} options
	 * @return {*}
	 * @memberof Axios
	 */
	static jsonp(options) {
		return new Promise((resolve, reject) => {
			JsonP(
				options.url,
				{
					param: 'callback',
				},
				function (err, response) {
					if (response.status == 'success') {
						resolve(response);
					} else {
						reject(response.messsage);
					}
				}
			);
		});
	}

	static async ajax(options: options) {
		/* 设置loading效果显示 */
		let loading;
		if (!options.loading) {
			loading = document.getElementById('ajaxLoading');
			loading.style.display = 'block';
		}
		let baseApi = Config.get().baseUrl;
		let result = await axios({
			url: options.url,
			method: (options.method || 'GET').toUpperCase(),
			baseURL: baseApi,
			timeout: Config.get().timeout,
			params: options.params,
		});
		loading = document.getElementById('ajaxLoading');
		loading.style.display = 'none';
		return result;
	}

	/**
	 * 有分页的封装
	 * @param _this 
	 * @param url 
	 * @param params 
	 * @param isMock 
	 */
	static requestList(_this, url, params, isMock) {
		var data = {
			params: params,
			isMock,
		};
		this.ajax({
			url,
			data,
		}).then((res: any) => {
			console.log(res.data);
			if (res && res.data) {
				let list = res.data.list.map((item, index) => {
					item.key = index;
					return item;
				});
				_this.setState({
					list,
					pagination: common.pagination(data, (current) => {
						_this.params.page = current;
						_this.requestList();
					}),
				});
			}
		});
	}
}
