import { Button, Col, Row } from 'antd';
import React from 'react';
import { Link } from 'react-router-dom';
import { common } from '../../common/public';
import './index.less';
import { connect } from 'react-redux';
interface Props {
    menuType?: any,
    history?: any,
    menuName?: any,
}

class Header extends React.Component<Props> {
    state = {
        userName: null,
        sysTime: null
    };

    componentWillMount() {
        this.setState({
            userName: '深海如梦'
        });
        setInterval(() => {
            let sysTime = common.getDateFormatStyle(new Date().getTime());
            this.setState({
                sysTime
            });
        }, 1000);
    }

    exit = () => {
        window.location.href = '//localhost:5760/#/login'
    }

    render() {
        const menuType = this.props.menuType;
        return (
            <div className="header">
                <Row className="header-top">
                    {
                        menuType ?
                            <Col span={6} className="logo">
                                <img src="/assets/logo-ant.svg" alt="" />
                                <span>共享汽车 通用管理系统</span>
                            </Col>
                            : null
                    }
                    <Col span={menuType ? 18 : 24}>
                        <span>欢迎，{this.state.userName}</span>
                        <Button type="link" style={{ marginLeft: 10, color: menuType ? '#f9f9f9' : null }} onClick={this.exit}>退出</Button>
                    </Col>
                </Row>
                {
                    menuType ? '' :
                        <Row className="breadcrumb">
                            <Col span="4" className="breadcrumb-title">
                                {this.props.menuName}
                            </Col>
                            <Col span="20" className="date">
                                时间： <span style={{ color: '#000000', fontWeight: 'bold' }}>{this.state.sysTime}</span>
                            </Col>
                        </Row>
                }
            </div>
        );
    }
}
const mapStateToPorps = (state: any) => {
    return {
        menuName: state.menuName
    }
}
export default connect(mapStateToPorps)(Header);