import React from 'react';
import menuList from '../../config/menuConfig';
import { Menu } from 'antd';

import './index.less';
import { NavLink } from 'react-router-dom';
import { connect } from 'react-redux';
import { switchMenu } from '../../redux/action';
const { SubMenu } = Menu;

interface Props {
    dispatch?: any,
}

class NavLeft extends React.Component<Props> {

    state = {
        menuTreeNode: [],
        currentKey: null,
    };

    componentDidMount() {
        // 获取菜单树，并渲染菜单
        const menuTreeNode = this.renderMemu(menuList);
        // 去除#和其他字符，保存当前路由名字，设置菜单选中，即刷新的时候菜单还是选中状态
        let currentKey = window.location.hash.replace(/#|\?.*$/g, '');
        this.setState({
            currentKey,
            menuTreeNode: menuTreeNode
        });
    }


    // 菜单渲染 菜单递归
    renderMemu = (data) => {
        return data.map((item) => {
            if (item.children) {
                return (
                    <SubMenu title={item.title} key={item.key}>
                        {this.renderMemu(item.children)}
                    </SubMenu>
                );
            }
            return <Menu.Item title={item.title} key={item.key}>
                <NavLink to={item.key}>{item.title}</NavLink>
            </Menu.Item>;
        });
    };

    handleClick = ({ item, key }) => {
        const { dispatch } = this.props;
        dispatch(switchMenu(item.props.title))
        this.setState({
            currentKey: key
        })
    }

    render() {
        const { menuTreeNode, currentKey } = this.state;
        return (
            <div>
                <div className="logo">
                    <img src="./assets/logo.svg" alt="" />
                    <h1>LCar System</h1>
                </div>
                {/* 菜单选中状态 */}
                <Menu mode="inline" theme="dark" selectedKeys={[currentKey]} onClick={this.handleClick}>
                    {menuTreeNode}
                </Menu>
            </div>
        );
    }
};
export default connect()(NavLeft);